.App {
  // text-align: center;
  .main-layout{
     display: grid;
     grid-template-columns: 165px 1fr 165px;
     div {
       grid-column-start: 2;
       grid-column-end: 3 ;
     }
  }
}
.header, .hero{
  background-color: #014e56;
 
}
.header{
  .main-container{
    display: grid;
    grid-template-columns: 1fr 1fr 3fr;
    .logo{
      width: 160px;
      height: 40px;
      grid-column: 1;
    }
    .links {
      width: 51px;
      height: 28px;
      color: #ffffff;
      font-family: "Livvic - Semi Bold";
      font-size: 18px;
      font-weight: 600;
      font-style: normal;
      letter-spacing: normal;
      line-height: 28px;
      text-align: left;
      grid-column: 2;
      display: flex;
      flex-direction: row;
      // justify-content: space-around;
      gap: 2em;
      margin-left: 2em;
      align-self: center;
    }
    .contact-or-hamburger{
      grid-column: 3;
      display: flex;
      justify-self: flex-end;
      align-self: center;
      width: 88px;
      height: 28px;
      color: #ffffff;
      font-family: "Livvic - Semi Bold";
      font-size: 18px;
      font-weight: 600;
      font-style: normal;
      letter-spacing: normal;
      line-height: 28px;
      // text-align: left;
      border: white solid 1px;
      border-radius: 30px;
      padding-inline: 15px;
    }
  }
  // display: flex;
  // flex-direction: row;
  // color: #ffffff;
}
.hero{
  .hero-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    .title{
      width: 635px;
      height: 201px;
      color: #ffffff;
      font-family: "Livvic - Bold";
      font-size: 100px;
      font-weight: 700;
      font-style: normal;
      letter-spacing: normal;
      line-height: 100px;
      text-align: left;
      /* Text style for "talent" */
      grid-column: 1;
      .talent-word{
        color: #f67e7e;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
      }
    }

  .content{
    display: grid;
    grid-template-rows: 1fr 1fr;
    width: 445px;
    height: 109px;
    color: #ffffff;
    font-family: "Livvic - Semi Bold";
    font-size: 18px;
    font-weight: 600;
    font-style: normal;
    letter-spacing: normal;
    line-height: 28px;
    text-align: left;
    .rectangle{
      width: 50px;
      height: 4px;
      background-color: #79c8c7;
    }
  }
  .image{
    .combBlue{
      width: 100px;
      height: 44px;
      // background-color: #79c8c7;
    }
    .combPink{
      width: 201px;
      height: 200px;
      // background-color: #f67e7e;
    }
    .combDark{
      width: 200px;
      height: 200px;
      // background-color: #f67e7e;
    }
    .oval{
      width: 200px;
      height: 200px;
      // background-color: #2c6269;
    }
    .rectangle{
      width: 43px;
      height: 43px;
      transform: rotate(180deg);
      // background-color: #79c8c7;
    }
  }
}
}


.build-and-manage{
  background-color: #012f34;
}
.delivering-real-results{
  background-color: #004047;
}
.ready-to-get-started{
  background-color: #f67e7e;
}
.footer{
  background-color: #002529;
}








